* {
  box-sizing: border-box;
}


body,
html {
  margin: 0;
  height: 100%;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  background-color: rgb(247, 250, 252);
  font-size: 13px;
}



#root {
  height: 100%;
}

.container {
  height: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  .card {
    width: 75%;
    background-color: #FFF;
    border: 1px solid #FFF;
    
    border-radius: 1.2em;
    -webkit-border-radius: 1.2em;
    -moz-border-radius: 1.2em;
  
    padding: 2em;
  }
  
  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .title {
    font-size: 1.99em;
    padding: .2em .6em;
  }
}



.button {
  background-color: #23649f;
  border: 1px solid #23649f;
  color: #FFF;

  border-radius: .7em;
  -webkit-border-radius: .7em;
  -moz-border-radius: .7em;

  font-size: 1.4em;
  font-weight: 400;

  padding: .5em 1em;
  cursor: pointer;
}

a, a:link {
  color: #FFF;
  text-decoration: none;
}

.add {
      
}


.quotes {

  margin-top: 1.2em;
  position: relative;

  display: flex;
  flex-direction: row;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .quote {
    & {
      font-size: 1.4em;
      border-bottom: 1px solid #dfdfdf;
      padding: .8em;
    }

    .quote-info {
      width: 75%;
    }
  
    .author {
      color: #8b8989;
    }

    .actions {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      width: 25%;

      .edit-button {
        cursor: pointer;
        width: 1em;
      }
  
      .delete-button {
        cursor: pointer;
        width: 1em;
      }
    }
  }
}

.error-message {
  color: #ff0000;
  font-size: 1.4em;
  margin-bottom: .5em;
}

.modal {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.model-header {
  height: 5%;
  width: 100%;
  border-bottom: 1px solid #000;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%; 
}

.form {
  height: 100%;
  width: 100%;
  margin-top: 1.2em;
  padding: .8em;

  .actions {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

 

  

  .submit {
    margin: 0 .8em;
    cursor: pointer;
  }

  .cancel {
    background-color: #cecece;
    border: 1px solid #cecece;
    color: #000000;
    cursor: pointer;
  }
}


.quote-add {
  position: absolute;
  left: 0;
  width: 0;
  height: 0;
  transition: 1s;
}

.quote-list:hover .quote-add {
  transition: 1s;
  left: 0;
  width: 100%;
  height: 100%;
}

.quote {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  
}

.trashcan {
  width: 20px;
  height: 20px;
  
  .trashcan-open {
    display: none;
  }
  
  &:hover {
    .trashcan-open {
      display: inline;
    }
    .trashcan-closed {
      display: none;
    }
  }
  
}


input {
  display: block;
  width: 100%;
  margin-bottom: 18px;
  height: 28px;
  padding-left: 6px;
}
